* {
  font-size: 10px;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
@media (max-width: 905px) {
  * {
    font-size: 9px;
  }
}
@media (max-width: 605px) {
  * {
    font-size: 8px;
  }
}
@media (max-width: 405px) {
  * {
    font-size: 9px;
  }
}

body {
  font-family: "Spartan", "sans-serif";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body.fixed {
  overflow: hidden;
}

a {
  text-decoration: none;
  color: #121212;
}

.wrapper {
  max-width: 1110px;
  height: 100%;
  margin: 0 auto;
}

h2 {
  font-size: 2.2rem;
}

h3 {
  font-size: 1.4rem;
}

h4 {
  font-size: 1.4rem;
  padding: 1rem 0;
}

img {
  width: 100%;
  height: 100%;
}

.topbar {
  width: 100%;
  background: #121212;
  color: #fff;
  opacity: 0.8;
}
.topbar__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.topbar__left {
  display: flex;
  align-items: center;
}
.topbar__item {
  margin: 0 0.5rem;
  display: flex;
  align-items: center;
}
.topbar__item p {
  font-size: 1.1rem;
  margin-left: 0.9rem;
  white-space: nowrap;
}
@media (max-width: 605px) {
  .topbar__item:last-child {
    display: none;
  }
}
.topbar__social {
  height: 100%;
  display: flex;
  align-items: center;
}
.topbar__social ul {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.topbar__social li {
  width: 2.3rem;
  height: 2.3rem;
  display: flex;
  justify-content: center;
  margin: 0 0.3rem;
  list-style: none;
}
.topbar__social li a {
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: #ffffff;
  background-position: center;
}
.topbar__social__facebook {
  background: url(./assets/img/facebook.png) no-repeat;
}
.topbar__social__twitter {
  background: url(./assets/img/twitter.png) no-repeat;
}
.topbar__social__instagram {
  background: url(./assets/img/instagram.png) no-repeat;
}
.topbar__social__noname {
  background: url(./assets/img/noname.png) no-repeat;
}
@media (max-width: 405px) {
  .topbar {
    display: none;
  }
}

.navigation {
  width: 100%;
  padding: 1.5rem 0 2rem 0;
}
.navigation__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navigation__logo {
  height: 100%;
  font-family: "Playfair Display SC", "sans-serif";
  font-style: normal;
  font-weight: normal;
  display: flex;
  justify-content: center;
  align-items: center;
}
.navigation__logo a {
  font-size: 2.4rem;
  color: #121212;
  text-decoration: none;
}
.navigation__user {
  height: 3rem;
  display: flex;
  align-items: center;
}
.navigation__user ul {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.navigation__user li {
  width: 3rem;
  height: 1.8rem;
  display: flex;
  justify-content: center;
  margin: 0 0.3rem;
  list-style: none;
}
.navigation__user li a {
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: #ffffff;
  background-position: center;
  background-size: contain;
}
.navigation__user li:last-child {
  display: none;
}
@media (max-width: 905px) {
  .navigation__user li:last-child {
    display: block;
    margin-left: 1rem;
  }
}
.navigation__user__search {
  background: url(./assets/img/search.png) no-repeat;
}
.navigation__user__planet {
  background: url(./assets/img/planet.png) no-repeat;
}
.navigation__user__person {
  background: url(./assets/img/person.png) no-repeat;
}
.navigation__user__basket {
  background: url(./assets/img/basket.png) no-repeat;
  position: relative;
}
.navigation__user__basket div {
  width: 65%;
  height: 100%;
  font-size: 0.9rem;
  font-weight: bold;
  position: absolute;
  top: -0.8rem;
  right: -0.5rem;
  border-radius: 50%;
  border: 0.2rem solid #ffffff;
  color: #fff;
  background-color: #121212;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  font-size: 1.4rem;
  transition: all 0.5s;
}
.menu li {
  margin: 0 1.4rem;
  font-size: 1.4rem;
  line-height: 16px;
}
.menu li a {
  font-size: 1.4rem;
}
@media (max-width: 905px) {
  .menu li a {
    font-size: 2rem;
  }
}
@media (max-width: 905px) {
  .menu li {
    line-height: 4.4rem;
  }
}
@media (max-width: 905px) {
  .menu {
    width: 100%;
    height: 100vh;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    padding: 3rem 0 0 0;
    text-align: left;
    transform: translateX(-100%);
    z-index: 100;
    background-color: #fff;
  }
}
@media (max-width: 405px) {
  .menu {
    padding: 1rem 0 0 0;
  }
}
.menu.visible {
  transform: translateX(0%);
}

.women {
  margin-bottom: 6rem;
}
.women__wrapper {
  display: flex;
  flex-direction: column;
}
.women__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 5rem;
}
.women__nav {
  display: flex;
  list-style: none;
  color: #121212;
  opacity: 0.6;
}
.women__nav li {
  font-size: 1.2rem;
  letter-spacing: 0.06em;
  margin-left: 3rem;
}
@media (max-width: 605px) {
  .women__nav {
    display: none;
  }
}
.women__group {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-content: space-between;
  flex-wrap: wrap;
  list-style: none;
  overflow: hidden;
}
.women__group li {
  width: 23.6%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 3rem;
}
.women__group li a {
  width: 100%;
  height: 100%;
  border: 1px solid transparent;
  transition: all 0.3s;
}
.women__group li a:hover {
  transform: scale(97%);
}
@media (max-width: 905px) {
  .women__group li {
    width: 48%;
  }
}
@media (max-width: 405px) {
  .women__group li {
    width: 97%;
  }
}
.women__group__title {
  flex: 1;
}
.women__group p {
  font-size: 1.4rem;
  color: #121212;
  opacity: 0.6;
  text-align: start;
  margin: 1.2rem 0;
}
.women__group__price {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.women__group__price p {
  font-weight: bold;
  font-size: 1.4rem;
  text-align: start;
  opacity: 1;
  display: flex;
  align-items: center;
}
.women__group__price p span {
  font-weight: bold;
  font-size: 1.2rem;
  opacity: 0.4;
  text-decoration: line-through;
  margin-left: 0.4rem;
}
.women__group__review {
  display: flex;
}
.women__group__foto {
  position: relative;
}
.women__group__sale {
  width: 5.5rem;
  height: 3.5rem;
  color: #FFF;
  font-size: 1.2rem;
  font-weight: bold;
  background-color: red;
  position: absolute;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 405px) {
  .women__group {
    justify-content: center;
  }
}
.women__footer {
  padding: 1.8rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.women__footer p {
  font-size: 1.2rem;
}
.women__route {
  padding: 0.8rem 0 0.8rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.women__route__name {
  display: flex;
}
.women__route__name a {
  font-size: 1.1rem;
  opacity: 0.6;
}
.women__route__name p {
  font-size: 1.1rem;
}
.women__route__share {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.women__route__share p {
  font-size: 1.1rem;
  line-height: 1px;
  margin-left: 0.9rem;
}
.women__filter {
  display: flex;
  justify-content: space-between;
  padding: 2.5rem 0 2.5rem 0;
}
.women__filter__icon {
  display: flex;
  align-items: center;
  opacity: 0.6;
}
.women__filter__icon i {
  width: 2.1rem;
  height: 2.1rem;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: url(./assets/img/filtericon.png);
}
.women__filter__icon p {
  font-size: 1.3rem;
  line-height: 14px;
  letter-spacing: 0.06em;
  color: #121212;
}
.women__filter__view {
  display: flex;
}
.women__filter__view i {
  width: 2.1rem;
  height: 2.1rem;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.women__filter__view i:first-child {
  opacity: 0.6;
  background-image: url(./assets/img/filter1.png);
}
.women__filter__view i:last-child {
  background-image: url(./assets/img/filter2.png);
}
.women__title {
  padding: 1rem 0 2.4rem 0;
}

