.collection {
  margin-bottom: 6rem;
}
.collection__wrapper {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 605px) {
  .collection__wrapper {
    flex-direction: column;
    align-items: center;
  }
}
.collection__item {
  height: 18vw;
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-position: top;
  background-size: cover;
  background-repeat: no-repeat;
}
.collection__item__half {
  width: 20.5vw;
  height: 5vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #FFFFFF;
  border-radius: 4px;
}
.collection__item:first-child {
  background-image: url(./assets/img/collection-left.png);
  margin-right: 1.5rem;
}
@media (max-width: 605px) {
  .collection__item:first-child {
    margin: 0 0 1rem 0;
  }
}
.collection__item:last-child {
  background-image: url(./assets/img/collection-rigth.png);
  margin-left: 1.5rem;
}
.collection__item:last-child span {
  color: red;
  font-size: 1.4rem;
}
@media (max-width: 605px) {
  .collection__item:last-child {
    margin: 1rem 0 0 0;
  }
}
@media (max-width: 605px) {
  .collection__item {
    width: 95%;
    height: 40vw;
  }
}

.subscribe {
  margin-bottom: 6rem;
  overflow: hidden;
}
.subscribe__wrapper {
  height: 41vw;
  background-image: url(./assets/img/subscribe-gradient.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: -100;
}
.subscribe__center {
  width: 37vw;
  height: 32vw;
  background: #fff;
  border-radius: 0.4rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.subscribe__center p {
  font-size: 1.2rem;
  text-align: center;
  margin-bottom: 1.6rem;
}
.subscribe__item {
  width: 23.8vw;
  height: 4.5vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 2.4rem;
}
.subscribe__item h3 {
  font-size: 1.6rem;
  font-weight: 500;
}
.subscribe__item:nth-child(2) {
  width: 100%;
}
.subscribe__item:nth-child(2) h3 {
  font-size: 2.3rem;
  font-weight: 500;
}
.subscribe__item:nth-child(2) span {
  font-size: 2.3rem;
  font-weight: 500;
  color: red;
}
.subscribe__item:nth-child(3) h3 {
  font-size: 1.6rem;
  opacity: 0.4;
}
.subscribe__item:last-child {
  background: #121212;
  color: #ffffff;
}
.subscribe__women {
  width: 22vw;
  height: 41vw;
  transform: translateX(3.5vw);
  margin-top: 5.5vw;
  background-image: url(./assets/img/subscribe-women.png);
  background-position: left;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: 100;
}
@media (max-width: 605px) {
