.hamburger-line {
  content: "";
  position: absolute;
  display: inline-block;
  width: 100%;
  height: 0.23rem;
  background: #1d4668;
  border-radius: 5px;
}

.hamburger {
  transition: 0.4s;
  display: none;
  position: relative;
  top: 0;
  width: 100%;
  height: 15px;
  z-index: 1000;
}
@media (max-width: 905px) {
  .hamburger {
    display: inline-block;
  }
}
.hamburger:before {
  transition: 0.5s;
  content: "";
  position: absolute;
  display: inline-block;
  width: 100%;
  height: 3px;
  background: #1d4668;
  border-radius: 5px;
  top: -1px;
  left: 0;
}
.hamburger:after {
  transition: 0.5s;
  content: "";
  position: absolute;
  display: inline-block;
  width: 100%;
  height: 3px;
  background: #1d4668;
  border-radius: 5px;
  bottom: -1px;
  left: 0;
}

.rotate:before {
  transition: 0.5s;
  content: "";
  position: absolute;
  display: inline-block;
  width: 100%;
  height: 0.23rem;
  background: #1d4668;
  border-radius: 5px;
  top: 1rem;
  left: 0;
  transform: rotate(405deg);
}
.rotate:after {
  transition: 0.5s;
  content: "";
  position: absolute;
  display: inline-block;
  width: 100%;
  height: 0.23rem;
  background: #1d4668;
  border-radius: 5px;
  bottom: 1.1rem;
  left: 0;
  transform: rotate(495deg);
}
.rotate .hamburger-line {
  opacity: 0;
}

.hamburger:hover {
  cursor: pointer;
}

.hamburger-line {
  content: "";
  position: absolute;
  display: inline-block;
  width: 100%;
  height: 3px;
  background: #1d4668;
  border-radius: 5px;
  top: calc(50% - 1.5px);
  left: 0;
}

